import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement } from 'chart.js';

// Register required components
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement);

const LineChart = () => {
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [],
  });

  useEffect(() => {
    // Fetch ISP data from Django API
    fetch('/api/isp-data/')
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        // Assuming `data` is an array of objects returned from Django view
        const labels = [];           // For storing country names
        const throughputData = [];  // For storing throughput values
        const rttData = [];         // For storing min_rtt values
        const packetLossData = [];  // For storing packet loss values

        // Iterate over the data to extract country names and other metrics
        data.forEach((item) => {
          labels.push(item.country);           // Store the country name as a label
          throughputData.push(item.throughput);   // Store the throughput value
          rttData.push(item.min_rtt);            // Store the min_rtt value
          packetLossData.push(item.packet_loss); // Store the packet loss value
        });

        // Update the chart data with labels and datasets
        setChartData({
          labels: labels,
          datasets: [
            {
              label: 'Throughput',
              data: throughputData,
              borderColor: 'rgba(75, 192, 192, 1)',
              backgroundColor: 'rgba(75, 192, 192, 0.4)',
              type: 'line',
            },
            {
              label: 'Min RTT',
              data: rttData,
              borderColor: 'rgba(255, 99, 132, 1)',
              backgroundColor: 'rgba(255, 99, 132, 0.4)',
              type: 'line',
            },
            {
              label: 'Packet Loss',
              data: packetLossData,
              borderColor: 'rgba(153, 102, 255, 1)',
              backgroundColor: 'rgba(153, 102, 255, 0.4)',
              type: 'line',
            },
          ],
        });
      })
      .catch((error) => console.error('Error fetching data:', error));
  }, []);

  const options = {
    scales: {
      x: {
        title: {
          display: true,
          text: "Country",
        },
        ticks: {
          autoSkip: false, // Ensure all x-axis labels are displayed
        },
      },
      y: {
        title: {
          display: true,
          text: "Metrics",
        },
        beginAtZero: true,
      },
    },
  };

  return <Line data={chartData} options={options} />;
};

export default LineChart;
